<template>
  <div class="publish-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!-- 面包屑路径导航 -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/">首页</el-breadcrumb-item>
          <el-breadcrumb-item>{{ $route.query.id ? '修改文章' : '发布文章' }}</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- /面包屑路径导航 -->
      </div>
      <el-form ref="publish-form" 
        :model="article" 
        label-width="60px"
        :rules="formRules"
      >
        <el-form-item label="标题" prop="title">
          <el-input v-model="article.title"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <el-input type="textarea" v-model="article.content"></el-input>
        </el-form-item>
        <el-form-item label="封面">
          <el-radio-group v-model="article.cover.type">
            <el-radio :label="1">单图</el-radio>
            <el-radio :label="3">三图</el-radio>
            <el-radio :label="0">无图</el-radio>
            <el-radio :label="-1">自动</el-radio>
          </el-radio-group>
          <!-- 
            需要把选择的封面图片的地址中放入 aritcle.cover.images 数组中

            当给事件处理函数传递了自定义的参数以后，就无法得到原本的数据参数

            如果想要在时间处理函数自定义传参以后还想得到原来的事件本身的参数，
            则手动指定 $event 它就代表事件本身的参数

            在组件上使用 v-model

            当需要给子组件提供的数据既要使用还要修改。这个时候可以使用 v-model 简化数据绑定
            v-model="article.cover.images[index]"
              给子组件传递了一个名字叫 value 的数据
              :value="article.cover.images[index]"
              默认监听 input 事件，当事件发生，会让绑定数据 = 事件参数
              @input="article.cover.images[index] = 事件参数"

            注意：v-model 仅仅是简写了，本质还是在父子组件通信
          -->
          <template v-if="article.cover.type > 0">
            <upload-cover 
              :key="cover"
              v-for="(cover, index) in article.cover.type"
              v-model="article.cover.images[index]"
            />
            <!-- <upload-cover 
              :key="cover"
              v-for="(cover, index) in article.cover.type"
              :cover-image="article.cover.images[index]"
              @update-cover="OnUpdateCover(index, $event)"
            /> -->
          </template>
        </el-form-item>
        <el-form-item label="频道" prop="channel_id">
          <el-select v-model="article.channel_id" placeholder="请选择频道">
            <el-option 
                :label="channel.name" 
                :value="channel.id"
                v-for="(channel, index) in channels"
                :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onPublish(false)">{{ $route.query.id ? '修改' : '发表' }}</el-button>
          <el-button @click="onPublish(true)">存入草稿</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { 
    getArticleChannels,
    addArticle,
    updateArticle,
    getArticle 
} from '@/api/article'

import UploadCover from './components/upload-cover'

export default {
  name: 'PublishIndex',
  components: {
    UploadCover
  },
  props: {},
  data () {
    return {
      channels: [], // 文章频道列表
      article: {
        title: '', // 文章标题
        content: '', // 文章内容
        cover: { // 文章封面
          type: 1, // 封面类型 -1:自动，0-无图，1-1张，3-3张
          images: [] // 封面图片的地址
        },
        channel_id: null
      },
      formRules: {
        title: [
          { required: true, message: '请输入文章标题', trigger: 'blur' },
          { min: 5, max: 30, message: '长度在 5 到 30 个字符', trigger: 'blur' }
        ],
        content: [
          // { required: true, message: '请输入文章内容', trigger: 'change' }
          {
            validator (rule, value, callback) {
              // console.log('content validator')
              if (value === '<p></p>') {
                // 验证失败
                callback(new Error('请输入文章内容'))
              } else {
                // 验证通过
                callback()
              }
            }
          },
          { required: true, message: '请输入文章内容', trigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '请选择文章频道' }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadChannels()

    // 由于发布和编辑使用的是同一个组件，所以我们需要判断
    // 如果路由参数中有 id，则请求展示文章内容
    if (this.$route.query.id) {
      this.loadArticle()
    } else {

    }
  },
  mounted () {},
  methods: {
    loadChannels () {
        getArticleChannels().then(res => {
            this.channels = res.data.data.channels
        })
    },
    // 修改文章：加载文章内容
    loadArticle () {
      getArticle(this.$route.query.id).then(res => {
        this.article = res.data.data
      })
    },
    onPublish (draft = false) {
      this.$refs['publish-form'].validate(valid => {
        if (!valid) {
          // 验证失败，停止提交表单
          return
        }
        // 验证通过，提交表单
        // 找到数据接口
        // 封装请求方法
        // 请求提交表单
        // 如果是修改文章，则执行修改操作，否则执行添加操作
        const articleId = this.$route.query.id
        if (articleId) {
            // 执行修改操作
            updateArticle(articleId, this.article, draft).then(res => {
            console.log(res)
            this.$message({
                message: `${draft ? '存入草稿' : '发布'}成功`,
                type: 'success'
            })
            // 跳转到内容管理页面
            this.$router.push('/article')
            })
        } else {
          addArticle(this.article, draft).then(res => {
          // 处理响应结果
          // console.log(res)
          this.$message({
              message: `${draft ? '存入草稿' : '发布'}成功`,
              type: 'success'
          })
          // 跳转到内容管理页面
          this.$router.push('/article')
          })
        }
      })
    },
    OnUpdateCover (index, url) {
      this.article.cover.images[index] = url
    }
  }
}
</script>

<style scoped lang="less"></style>