<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!-- 面包屑路径导航 -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/">首页</el-breadcrumb-item>
          <el-breadcrumb-item>个人设置</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- /面包屑路径导航 -->
      </div>
      <el-row :gutter="10">
        <el-col :span="15">
          <el-form ref="user" :model="user" label-width="80px">
            <el-form-item label="编号">
              {{ user.id }}
            </el-form-item>
            <el-form-item label="手机">
              {{ user.mobile }}
            </el-form-item>
            <el-form-item label="媒体名字">
              <el-input v-model="user.name"></el-input>
            </el-form-item>
            <el-form-item label="媒体介绍">
              <el-input type="textarea" v-model="user.intro"></el-input>
            </el-form-item>
            <el-form-item label="邮箱">
              <el-input v-model="user.email"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button 
                type="primary" 
                @click="onUpdateUser"
                :loading="updateProfileLoading"
              >保存</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="6" :offset="2">
          <!-- <p @click="$refs.file.click()">修改头像</p> -->
          <label for="file">
            <el-avatar 
              shape="square" 
              :size="150" 
              fit="cover" 
              :src="user.photo"
            ></el-avatar>
          </label>
          <p @click="$refs.file.click()">修改头像</p>
          <input 
            id="file"
            type="file"
            ref="file" 
            hidden
            @change="onFileChange"
          >
        </el-col>
      </el-row>
    </el-card>
    <el-dialog
      title="修改头像"
      :visible.sync="dialogVisible"
      @opened="onDialogOpened"
      @closed="onDialogClosed"
      append-to-body
    >
      <div class="preview-image-wrap">
        <img 
          class="preview-image"
          :src="previewImage" 
          ref="preview-image"
          alt="" 
        >
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button 
          type="primary" 
          :loading="updatePhotoLoading"
          @click="onUpdatePhoto"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { 
  getUserProfile,
  updateUserPhoto,
  updateUserProfile 
} from '@/api/user'
import gloalBus from '@/utils/global-bus'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
// 获取图片 DOM 节点
// const image = this.$refs['preview-image']

// 初始化裁切器
// const cropper = new Cropper(image, {
//   aspectRatio: 16 / 9,
//   crop(event) {
//     console.log(event.detail.x);
//     console.log(event.detail.y);
//     console.log(event.detail.width);
//     console.log(event.detail.height);
//     console.log(event.detail.rotate);
//     console.log(event.detail.scaleX);
//     console.log(event.detail.scaleY);
//   },
// });
export default {
  name: "AccountIndex",
  components: {},
  props: {},
  data() {
    return {
      user: {
        email: '',
        id: null,
        intro: '',
        mobile: '',
        name: '',
        photo: ''
      }, // 用户资料
      dialogVisible: false, // 控制上传图片裁切预览的显示状态
      previewImage: '', // 预览图片
      cropper: null, // 裁切器实例
      updatePhotoLoading: false, // 更新用户头像 loading 状态
      updateProfileLoading: false // 更新基本信息的 loading 状态
    };
  },
  computed: {},
  watch: {},
  created() {
    this.loadUser()
  },
  methods: {
    onUpdateUser() {
      // 表单验证
      // 开启 loading 状态
      this.updateProfileLoading = true
      // 验证通过，提交表单
      updateUserProfile(this.user).then(res => {
        this.$message({
          type: 'success',
          message: '保存成功'
        })
        // 关闭 loading 状态
        this.updateProfileLoading = false

        // 发布通知，用户信息已经修改
        gloalBus.$emit('update-user', this.user)
      })
    },
    loadUser () {
      getUserProfile().then(res => {
        // console.log(res)
        this.user = res.data.data
      })
    },
    onFileChange () {
      // console.log('File Change')
      // 处理图片预览
      const file = this.$refs.file
      const blobData = window.URL.createObjectURL(file.files[0])
      
      this.previewImage = blobData

      // 展示弹出层，预览用户选择的图片
      this.dialogVisible = true

      

      // 解决选择相同文件不触发 change 事件问题
      this.$refs.file.value = ''
    },
    onDialogOpened () {
      // 图片裁切器必须基于 img 进行初始化
      // 注意：img 必须是可见状态才能正常完成初始化
      //      这里要在对话框里面初始化裁切器
      //      务必要在对话框完全打开的状态下进行初始化
      // 获取图片 DOM 节点
      const image = this.$refs['preview-image']

      // 第一次初始化好以后，第一次预览裁切的图片发生了变化
      // 裁切器默认不会更新
      // 如果需要预览图片发生变化更新裁切器：
      //    方式一：裁切器.replace 方法
      //    方式二：销毁裁切器，重新初始化
      // 初始化裁切器
      if (this.cropper) {
        this.cropper.replace(this.previewImage)
        return 
      }
      this.cropper = new Cropper(image, {
        // 不能移出图片
        viewMode: 1,
        // move 移动画布
        dragMode: 'none',
        // 比例
        aspectRatio: 1,
        // true 允许移动
        cropBoxMovable: true,
        // 缩放
        cropBoxResizable: false,
        background: false,
        // movable: true

        // 移动裁切器的时候会触发调用 crop 方法
        // crop(event) {
        //   console.log(event.detail.x);
        //   console.log(event.detail.y);
        //   console.log(event.detail.width);
        //   console.log(event.detail.height);
        //   console.log(event.detail.rotate);
        //   console.log(event.detail.scaleX);
        //   console.log(event.detail.scaleY);
        // }
      })
    },
    onDialogClosed () {
      // 对话框关闭，销毁裁切器
      this.cropper.destroy()
    },
    onUpdatePhoto () {
      // 让确认按钮开始 loading
      this.updatePhotoLoading = true
      // 获取裁切的图片对象
      this.cropper.getCroppedCanvas().toBlob(file => {
        const fd = new FormData()
        fd.append('photo', file)
        // 请求更新用户头像 请求提交 fd
        updateUserPhoto(fd).then(res => {
          // 关闭对话框
          this.dialogVisible = false
          // 更新视图的展示

          // 直接把裁剪结果对象转为 blob 数据本地预览
          this.user.photo = window.URL.createObjectURL(file)

          // 关闭确定按钮的 loading
          this.updatePhotoLoading = false

          this.$message({
            type: 'success',
            message: '更新头像成功'
          })

          // 更新顶部用户登录信息
          gloalBus.$emit('update-user', this.user)
          // console.log(res)
          // 把服务端返回的图片进行展示有些慢
          // this.user.photo = res.data.data.photo
        })
      })
      
    }
  }
};
</script>

<style scoped lang="less">
.preview-image-wrap {
  .preview-image {
    /* Ensure the size of the image fit the container perfectly */
    display: block;
    /* This rule is very important, please don't ignore this */
    max-width: 100%;
    height: 200px;
  }
}
</style>