<template>
  <el-container class="layout-container">
    <el-aside
      class="aside"
      width="auto">
      <AppAside 
        class="aside-menu"
        :is-collapse="isCollapse"/>
      <!-- <app-aside /> -->
    </el-aside>
    <el-container>
      <el-header class="header">
        <!-- <AppHeader /> -->
      <div class="header-container">
        <div>
          <!-- 
            class 样式处理
              {
                css 类名: 布尔值 
              }
              true：作用类名
              false：不作用类名
           -->
          <i 
            :class="{
              'el-icon-s-fold': isCollapse,
              'el-icon-s-unfold': !isCollapse
            }"
            @click="isOpen"></i>
          <span>江苏传智播客科技教育有限公司</span>
        </div>
        <el-dropdown>
          <div class="avatar-wrap">
            <img class="avatar" :src="user.photo" alt="">
            <span>{{ user.name }}</span>
            <i class="el-icon-arrow-down el-icon--right"></i>
          </div>
          <!-- 
            组件默认不识别原生事件的，除非内部做了处理
           -->
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>
              设置</el-dropdown-item>
            <el-dropdown-item
              @click.native="onLogout"
            >退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      </el-header>
      <el-main class="main">
        <!-- 子路由出口 -->
        <router-view />
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import AppAside from './components/aside'
import { getUserProfile } from '@/api/user'
import gloalBus from '@/utils/global-bus'
 
export default {
  name: 'LayoutIndex',
  components: {
    AppAside
  },
  props: {},
  data () {
    return {
      user: {}, // 当前用户信息
      isCollapse: false // 侧边菜单栏的展开状态
    }
  },
  computed: {},
  watch: {},
  created () {
    // 组件初始化好，请求获取用户资料
    this.loadUserProfile()

    // 注册自定义事件
    // 当这个事件发布以后，这个注册函数就会被调用到
    gloalBus.$on('update-user', (data) => {
      // console.log('update-user', data)
      this.user.name = data.name
      this.user.photo = data.photo
    })
  },
  mounted () {},
  methods: {
    // 除了登录接口，其他所有接口都需要授权才能够请求使用
    // 或者说除了登录，其他接口都需要提供你的身份令牌，才能获取数据
    loadUserProfile () {
      getUserProfile().then(res => {
        // console.log(res)
        this.user = res.data.data
      })
    },
    isOpen () {
      this.isCollapse = !this.isCollapse
    },
    onLogout () {
      this.$confirm('确认退出吗？', '退出提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 把用户的登录状态清除
        window.localStorage.removeItem('user')

        // 跳转到登录页面
        this.$router.push('/login')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消退出'
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
.layout-container {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.aside {
  background-color: #d3dce6;
  .aside-menu {
    height: 100%;
  }
}

.header {
  // background-color: #b3c0d1;
  border-bottom: 1px solid #ccc;
}

.main {
  background-color: #e9eef3;
}

.header-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ccc;
  .avatar-wrap {
    display: flex;
    align-items: center;
    .avatar {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      margin-right: 10px;
    }
  }
}
</style>