<template>
  <div class="image-list">
    <div class="action-head">
      <el-radio-group 
          v-model="collect" 
          size="mini"
          @change="onCollectChange"
      >
        <el-radio-button 
          :label="false"
        >全部</el-radio-button>
        <el-radio-button 
          :label="true"
        >收藏</el-radio-button>
      </el-radio-group>
      <el-button 
          v-if="isShowAdd"
          size="mini"
          type="success"
          @click="dialogUploadVisible = true"
      >上传素材</el-button>
    </div>
    <!-- 素材列表 -->
    <el-row :gutter="10">
      <el-col 
          class="image-item"
          :xs="12" 
          :sm="6" 
          :md="6" 
          :lg="4"
          v-for="(img, index) in images"
          :key="index"
      >
      <!-- :preview-src-list="new Array(img.url)" -->
        <el-image
          style="height: 150px"
          :src="img.url"
          fit="fit"
          @click.native="onImageSelected(index)"
        ></el-image>
        <div 
          class="selected"
          v-if="isShowSelected && selected === index"
        >
          <!-- 
            给 selected 的背景图进行定位
            浮于素材的上方
          -->
        </div>
        <div  v-if="isShowAction" class="image-action">
            <el-button 
              type="warning"
              :icon="img.is_collected ? 'el-icon-star-on' : 'el-icon-star-off'"
              circle
              size="mini"
              @click="onCollect(img)"
              :loading="img.loading"
          ></el-button>
            <!-- <i :class="{
                'el-icon-star-on': img.is_collected,
                'el-icon-star-off': !img.is_collected
              }"
              @click="onCollect(img)"
            ></i> -->
            <!-- <i class="el-icon-delete-solid"></i> -->
            <el-button 
              circle
              type="danger"
              size="mini"
              icon="el-icon-delete"
              :loading="img.loading"
              @click="onDelete(img)"
          ></el-button>
          </div>
        </el-col>
      </el-row>
      <!-- /素材列表 -->

      <!-- 列表分页 -->
      <!-- 
        total 用来设置总数据的条数
        它默认按照 10 条每页计算页码
        page-size 每页显示条目个数，支持 .syn 修饰符，默认每页 10 条
        分页数据改变以后，页码不会变化
        需要单独处理高亮的页码
       -->
      <el-pagination
        layout="prev, pager, next"
        background
        :total="totalCount"
        :page-size="pageSize"
        :current-page.sync="page"
        @current-change="onPageChange"
      >
      </el-pagination>
      <!-- /列表分页 -->

      <el-dialog 
        :append-to-body="true"
        title="上传素材" 
        :visible.sync="dialogUploadVisible">
        <!-- 
            upload 本身就支持请求提交上传操作
            使用它不用自己发送请求，会自己发
            请求方法：默认就是 POST
            请求路径：action，必须写完整路径
            请求头：headers
         -->
        <el-upload
            v-if="dialogUploadVisible"
            class="upload-demo"
            drag
            action="http://api-toutiao-web.itheima.net/mp/v1_0/user/images"
            :headers="uploadHeaders"
            name="image"
            :show-file-list="false"
            :on-success="onUploadSuccess"
            multiple>
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </el-dialog>
  </div>
</template>

<script>
import { 
    getImages,
    collectImage,
    deleteImage
} from '@/api/image'

export default {
  name: 'ImageIndex',
  components: {},
  // 使用数组声明 prop 不建议，不够严谨，功能不够强大，仅适合 demo 显示
  // 使用对象的方式定义 prop 更加严谨，功能更加丰富
  props: {
    // 是否显示添加素材
    isShowAdd: {
      type: Boolean, // 布尔值
      default: true // 默认值
    },

    // 是否显示图片下方的操作（收藏和删除）
    isShowAction: {
      type: Boolean,
      default: true 
    },

    isShowSelected: {
      type: Boolean,
      default: false
    }
  },
  data () {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      collect: false, // 默认查询全部素材列表 
      images: [], // 图片素材列表
      dialogUploadVisible: false,
      uploadHeaders: {
          Authorization: `Bearer ${user.token}`
      },
      totalCount: 0, // 总数据条数
      pageSize: 12, // 每页大小
      page: 1, // 当前代码
      selected: null // 选中的索引
    }
  },
  computed: {},
  watch: {},
  created () {
      this.loadImage(1)
  },
  mounted () {},
  methods: {
    loadImage (page = 1) {
        // 重置高亮页码，防止数据和页码不对应
        this.page = page
        getImages({
            collect: this.collect,
            page: page,
            per_page: this.pageSize
        }).then(res => {
            //   console.log(res)
            const results = res.data.data.results
            results.forEach(img => {
                // img 本来没有 loading 数据
                // 我们这里手动往这里添加该数据用来控制每一个收藏按钮的 loading 状态
                img.loading = false
            })
            this.images = results
            this.totalCount = res.data.data.total_count
        })
    },
    onUploadSuccess () {
        // 关闭对话框
        this.dialogUploadVisible = false
        // console.log(this.dialogUploadVisible)
        // 更新素材列表
        this.loadImage(this.page)
        this.$message({
            type: 'success',
            message: '上传成功'
        })
    },
    onCollect (img) {
        // 展示 loading
        img.loading = true
        // console.log(img)
        collectImage(img.id, !img.is_collected).then(res => {
            // 更新视图状态
            img.is_collected = !img.is_collected
            // 关闭 loading 
            img.loading = false
            // console.log(img)
        })
        // if (img.is_collected) {
        //     // 已收藏，取消收藏
        //     collectImage(img.id, false)
        // } else {
        //     // 没有收藏，添加收藏
        //     collectImage(img.id, true)
        // }
    },
    onDelete (img) {
        img.loading = true
        deleteImage(img.id).then(res => {
            // 重新加载数据列表
            this.loadImage(this.page)
            img.loading = false
        })
    },
    onCollectChange () {
        this.loadImage(1)
    },
    onPageChange (page) {
        this.loadImage(page)
    },
    onImageSelected (index) {
      this.selected = index
      // console.log('select', this.selected)
    }
  }
}
</script>

<style scoped lang="less">
.action-head {
    padding-bottom: 20px;
    display: flex;
    justify-content: space-between;
}
.image-item {
    position: relative;
}
.image-action {
    font-size: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    height: 40px;
    background-color: rgba(204, 204, 204, .5);
    position: absolute;
    bottom: 4px;
    left: 5px;
    right: 5px;
}
.selected {
  background: url(./selected.png) no-repeat;
  background-size: cover; 
  // 定位浮于素材上方 
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  display: flex;
  justify-content: center;
}
</style>
