import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/login/'
import Home from '@/views/home/'
import Layout from '@/views/layout/'
import Article from '@/views/article/'
import Publish from '@/views/publish'
import Image from '@/views/image'
import Comment from '@/views/comment'
import Settings from '@/views/account'
import Fans from '@/views/fans'
import NProgress from 'nprogress'

Vue.use(VueRouter)

// 路由配置表
const routes = [
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/',
    // 命名路由有一个 layout 有一个默认子路由，这个名字没有意义，所以警告
    // 正确做法：如果有默认子路由，就不要给父路由起名字了
    // name: 'layout',
    component: Layout,
    // 设置子路由渲染到副路由的出口
    children: [
      {
        // 我们通常会把根路径 / 设置为网站的首页
        // 我们在手动输入网址访问网站的时候，可以省略 /
        // 如果是别的名称，则必须加上
        path: '', // path 为空会作为默认子路由渲染
        // 路由名字的含义：使用动态路由比较方便
        name: 'home',
        component: Home
      },
      {
        path: '/article',
        name: 'article',
        component: Article
      },
      {
        path: '/publish',
        name: 'publish',
        component: Publish
      },
      {
        path: '/image',
        name: 'image',
        component: Image
      },
      {
        path: '/comment',
        name: 'comment',
        component: Comment
      },
      {
        path: '/settings',
        name: 'settings',
        component: Settings
      },
      {
        path: '/fans',
        name: 'fans',
        component: Fans
      }
    ]
  },
]

const router = new VueRouter({
  routes
})

// 路由导航守卫：说白了所有页面的导航都会经过这里
// 守卫页面的导航的
// to：要去的路由信息
// from：来自哪里的路由信息
// next：放行方法
router.beforeEach((to, from, next) => {
  // 开启顶部导航进度条特效
  NProgress.start()

  // 如果要访问的页面不是 /login，校验登录状态
  // 如果没有登录，则跳转到登录页面
  // 如果登录了，则允许通过
  // 允许通过
  // next()

  // console.log(to)
  // console.log(from)
  const user = JSON.parse(window.localStorage.getItem('user'))
  // const user = null;

  // 校验非登录页面的登录状态
  if (to.path !== '/login') {
    if (user) {
      // 已登录，允许通过
      next()
    } else {
      // 没有登录，跳转到登录页面
      next('/login')
    }
  } else {
    // 登录页面，正常允许通过
    next()
  }
})

router.afterEach((to, from) => {
  // 结束顶部的导航进度条
  NProgress.done();
});

export default router
