/**
 * 全局通信组件
 * 呼叫中心
 * 作用：可以让任何组件相互通信
 */
import Vue from "vue"

// 直接导出一个空的 Vue 实例
export default new Vue()

/**
 * 假设 a 组件要给 b 组件发送数据
 * b 注册通信的事件
 *      import gloalBus from '@/utils/global-bus'
 *      globalBus.$on('自定义事件名称', res => {
 *          // 处理函数
 *      })
 * a 发布通信的事件
 *      import gloalBus from '@/utils/global-bus'
 *       globalBus.$emit('自定义时间名称', 可选的数据)
 * 
 * 注意：通信的两端所使用的事件名称必须一致，否则是无效的
 * 
 * 
 * 反之，b 给 a 发送数据
 * a 注册通信事件
 * b 发布通信事件   
 */